import { Component } from '@angular/core';
import { RelogioAnalogicoService } from '../../services/relogio-analogico.service';
import { interval } from 'rxjs';

@Component({
  selector: 'app-relogio-analogico',
  templateUrl: './relogio-analogico.component.html',
  styleUrls: ['./relogio-analogico.component.css']
})

export class RelogioAnalogicoComponent {

  horaAtual: string;
  dataDoDia: string;

  constructor(private props: RelogioAnalogicoService ) {
    const secondsCounter = interval(1000);
    secondsCounter.subscribe(val => {
      this.horaAtual = this.props.horaAtual;
      this.dataDoDia = this.props.dataDoDia;
    });
  }
}
