import { Injectable } from '@angular/core';
import { interval } from 'rxjs';
import { DataHoraAtualService } from '../services/data-hora-atual.service';

@Injectable({
  providedIn: 'root'
})

export class RelogioAnalogicoService {

  horaAtual: string;
  dataDoDia: string;

  constructor(private props: DataHoraAtualService) {
    const secondsCounter = interval(1000);
    secondsCounter.subscribe(val => {
      this.setData();
    });
  }

  setData() {
    this.horaAtual = this.props.horaAtual;
    this.dataDoDia = this.props.dataDoDia;
    this.clock();
  }

  clock(): void {
    const Hs = '....';
    const H = Hs.split('');
    const Ms = '.....';
    const M = Ms.split('');
    const Ss = '......';
    const S = Ss.split('');
    const Ypos = 0;
    const Xpos = 0;
    const Ybase = 8;
    const Xbase = 8;
    const dots = 12;
    const time = new Date();
    const secs = time.getSeconds();
    const mins = time.getMinutes();
    const hr = time.getHours();
    const sec = -1.57 + Math.PI * secs / 30;
    const min = -1.57 + Math.PI * mins / 30;
    const hrs = -1.57 + Math.PI * hr / 6 + Math.PI * Number(time.getMinutes()) / 360;
    for (let i = 0; i < dots; ++i) {
      document.getElementById('dig' + (i + 1)).style.top = 0 - 15 + 40 * Number(Math.sin(-0.49 + dots + i / 1.9).toString()) + 'px';
      document.getElementById('dig' + (i + 1)).style.left = 0 - 14 + 40 * Number(Math.cos(-0.49 + dots + i / 1.9).toString()) + 'px';
    }
    for (let i = 0; i < S.length; i++) {
      document.getElementById('sec' + (i + 1)).style.top = Ypos + i * Ybase * Number(Math.sin(sec).toString()) + 'px';
      document.getElementById('sec' + (i + 1)).style.left = Xpos + i * Xbase * Number(Math.cos(sec).toString()) + 'px';
    }
    for (let i = 0; i < M.length; i++) {
      document.getElementById('min' + (i + 1)).style.top = Ypos + i * Ybase * Number(Math.sin(min).toString()) + 'px';
      document.getElementById('min' + (i + 1)).style.left = Xpos + i * Xbase * Number(Math.cos(min).toString()) + 'px';
    }
    for (let i = 0; i < H.length; i++) {
      document.getElementById('hour' + (i + 1)).style.top = Ypos + i * Ybase * Number(Math.sin(hrs).toString()) + 'px';
      document.getElementById('hour' + (i + 1)).style.left = Xpos + i * Xbase * Number(Math.cos(hrs).toString()) + 'px';
    }
  }
}
