import { Component, OnInit } from '@angular/core';
import { DataHoraAtualService } from '../../services/data-hora-atual.service';
import { interval } from 'rxjs';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  dataDoDia: string;
  horaAtual: string;

  constructor( private props: DataHoraAtualService ) {
    const secondsCounter = interval(1000);
    secondsCounter.subscribe(() => {
      this.dataDoDia = this.props.dataDoDia;
      this.horaAtual = this.props.horaAtual;
    });
  }

  ngOnInit(): void { }

}
