import { Component, ViewChild, AfterViewInit } from '@angular/core';
import Typewriter from 't-writer.js';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.css']
})
export class InicioComponent implements AfterViewInit{

  slogan = 'TODA EMPRESA';
  slogan2 = ' PRECISA DEIXAR SUA MARCA NA INTERNET!';
  slogan3 = ' PRECISA TER PRESENÇA NA WEB!';
  slogan4 = ' SABE QUE PODE CONTAR CONOSCO!';
  subslogan = 'Conhecemos os desafios das pequenas, médias e grandes empresas atuais e com essas experiências, juntamos as melhores técnicas para criar o marketing digital de sucesso!';
  subslogan1 = 'Conte conosco na hora de planejar as melhores maneiras para o seu negócio!';

  constructor() {}
  @ViewChild('subslogan') typewriterElement: { nativeElement: any; };
  @ViewChild('subslogan1') typewriterElement1: { nativeElement: any; };

  @ViewChild('slogan') typewriterElement2: { nativeElement: any; };
  @ViewChild('comboslogan') typewriterElement3: { nativeElement: any; };

  ngAfterViewInit() {
    const target = this.typewriterElement.nativeElement;
    const target1 = this.typewriterElement1.nativeElement;

    const target2 = this.typewriterElement2.nativeElement;
    const target3 = this.typewriterElement3.nativeElement;


    // SLOGAN
    const writer2 = new Typewriter(target2, {
      typeColor: 'white'
    });

    const writer3 = new Typewriter(target3, {
      typeColor: 'white'
    });

    writer2
    .type(this.slogan)
    .removeCursor()
    .then(writer3.start.bind(writer3))
    .start();

    writer3
      .type(this.slogan2)
      .rest(1000)
      .clear()
      .changeTypeColor('white')
      .type(this.slogan3)
      .rest(1000)
      .clear()
      .changeTypeColor('white')
      .type(this.slogan4)
      .rest(1000)
      .clear()
      .changeTypeColor('white')
      .clear()
      .then(writer2.start.bind(writer2));

    // SUBSLOGAN 0
    const writer = new Typewriter(target, {
      loop: false,
      typeColor: 'white',
      typeSpeed: 10
    });

    writer
      .type(this.subslogan)
      .rest(1000)
      .start();

    // SUBSLOGAN 1
    const writer1 = new Typewriter(target1, {
      loop: true,
      typeColor: 'white',
      typeSpeed: 10,
      deleteSpeed: 5,
    });

    writer1
      .type(this.subslogan1)
      .rest(10000)
      .start();
  }
}
