import { Component, OnInit } from '@angular/core';
import { faCaretUp } from '@fortawesome/free-solid-svg-icons';


declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  faCaretUp = faCaretUp;

    ngOnInit(): void {
      /* tslint:disable */
      $(document).ready(() => {
        let mousePos = {
          x: null,
          y: null
        };

        function getRandomInt(min, max) {
          return Math.round(Math.random() * (max - min + 1)) + min;
        }

        $(window).mousemove((e) => {
          mousePos.x = e.pageX;
          mousePos.y = e.pageY;
        });

        $(window).mouseleave((e) => {
          mousePos.x = -1;
          mousePos.y = -1;
        });

        let draw = setInterval(() => {
          if (mousePos.x > 0 && mousePos.y > 0) {
            let range = 10;

            let color = 'background: rgb(' + 0 + ',' + getRandomInt(0, 205) + ',' + getRandomInt(0, 205) + ');';

            let sizeInt = getRandomInt(10, 30);
            let size = 'height: ' + sizeInt + 'px; width: ' + sizeInt + 'px;';

            let left = 'left: ' + getRandomInt(mousePos.x - range - sizeInt, mousePos.x + range) + 'px;';

            let top = 'top: ' + getRandomInt(mousePos.y - range - sizeInt, mousePos.y + range) + 'px;';

            let style = left + top + color + size;
            $('<div class=\'ball\' style=\'' + style + '\'></div>')
              .appendTo('#wrap')
              .one( 'webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', () => { $(this).remove(); });
          }
        }, 1);
      });
    }

}
