import { Injectable } from '@angular/core';
import { interval } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataHoraAtualService {

  dataDoDia: string;
  horaAtual: string;

  constructor() {
    const secondsCounter = interval(1000);
    secondsCounter.subscribe(val => {
      this.setDate();
    });
  }

  setDate() {
    this.horaAtual = this.getData();
    this.dataDoDia = this.getHora();
  }

  getHora(): string {
    const time: Date = new Date();
    const h: any = time.getHours();
    let m: any = time.getMinutes();
    let s: any = time.getSeconds();
    m = this.checkTime(m);
    s = this.checkTime(s);
    return h + ':' + m + ':' + s;
  }

  // if numbers < 10 = adiciona "0"
  checkTime(i: any): string {
    if (i < 10) {
       i = '0' + i;
    }
    return i;
  }

  getData(): string {
    const data: Date = new Date();
    const dia: any = data.getDate().toString().padStart(2, '0');
    const mes: any = (data.getMonth() + 1).toString().padStart(2, '0'); // +1 pois no getMonth Janeiro começa com zero.
    const ano: any = data.getFullYear();
    return dia + '/' + mes + '/' + ano;
  }

}
